#content{padding:50px 0;background-color:#fff6f6}#content h1{text-align:center;font-size:2rem;margin-bottom:20px;position:relative;font-weight:bold}#content h1::after{content:"";display:block;width:50px;height:3px;background-color:#56397b;margin:10px auto 0}#content .newsCard{padding-top:20px;display:flex;gap:20px;flex-wrap:wrap;justify-content:space-evenly}#content .card{display:flex;flex-direction:column;justify-content:space-between;padding:20px}#content .newsCard div{background-color:#fff6f6;border-radius:10px;box-shadow:0 4px 8px rgba(0,0,0,.1);width:300px;overflow:hidden;position:relative;transition:transform .3s ease}#content .newsCard div:hover{transform:translateY(-10px)}#content .newsCard div img{width:100%;height:200px;object-fit:cover;border-radius:10px}#content .newsCard div p{padding-top:10px;height:100px;font-size:1rem;line-height:1.5;color:#333}#content .newsCard div span{color:#666;font-size:12px}#content .newsCard div .end{display:flex;align-items:center;justify-content:space-between;padding-top:10px}#content .buttonContainer{display:flex;justify-content:center;padding-top:40px}#content .buttonContainer button{padding:16px 30px;background-color:#56397b;color:#fff;border:none;border-radius:16px;cursor:pointer;transition:background-color .3s ease}#content .buttonContainer button:hover{background-color:#452d69}@media(max-width: 430px){#content .newsCard{flex-wrap:nowrap;overflow-x:auto;gap:1px}#content .newsCard .card{margin-right:20px;min-width:300px}#content .newsCard .card:first-child{margin-left:1620px}#content .newsCard::-webkit-scrollbar{display:none}}@media(max-width: 767px){#content .newsCard{justify-content:center}}@media(min-width: 768px){#content .newsCard{justify-content:center}}@media(min-width: 1024px){#content .newsCard{justify-content:center}}